.Product-item {
    max-height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid grey;
    width: 280px;
    background-color: white;
    margin: 10px;
    padding: 10px;
}

.Product-title {
    padding: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 5px;
    max-width: 100%;
}

.Product-price {
    margin-bottom: auto;
}

.Product-image {
    margin-top: 10px;
    width: 200px;
    margin-bottom: 5px;
}

.Product-price_total {
    margin: 10px 0;
    font-size: 10px;
    font-weight: bold;
    color: rgb(69, 69, 69);
}

.Add-product {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 70%;
}

.Btn-add {
    width: 70px;
    height: 30px;
    background-color: cadetblue;
    border: none;
    font-size: 20px;
    color: white;
    border-radius: 10px;
}

.Btn-add:hover {
    cursor: pointer;
    background-color: rgb(215, 191, 162);
}